import React, { Component } from "react";
import { Grid, Row, Col } from "react-bootstrap";
import Modal from "./Modal";
import PropTypes from "prop-types";

class Images extends Component {
  static defaultProps = {
    images: [],
    hideOverlay: false,
    renderOverlay: () => "",
    renderVideoOverlay: () => "Play",
    overlayBackgroundColor: "#222222",
    onClickEach: null,
    countFrom: 5
  };

  constructor(props) {
    super(props);

    this.state = {
      modal: false,
      countFrom:
        props.countFrom > 0 && props.countFrom < 5 ? props.countFrom : 5,
      conditionalRender: false
    };

    this.openModal = this.openModal.bind(this);
    this.onClose = this.onClose.bind(this);

    if (props.countFrom <= 0 || props.countFrom > 5) {
      console.warn("countFrom is limited to 5!");
    }
  }

  openModal(index) {
    const { onClickEach, images } = this.props;

    if (onClickEach) {
      return onClickEach({ src: images[index], index });
    }

    this.setState({ modal: true, url: images[index], index });
  }

  onClose() {
    this.setState({ modal: false });
  }

  renderOne() {
    const { images } = this.props;
    const { countFrom } = this.state;
    const overlay =
      images.length > countFrom && countFrom == 1
        ? this.renderCountOverlay(true)
        : this.renderOverlay();

    return (
      <Grid>
        <Row>
          <Col xs={12} md={12} className="height-one">
            <div
              className="image background"
              onClick={this.openModal.bind(this, 0)}
              style={{ background: `url(${images[0].source})` }}
            >
              {this.renderVideoOverlay(images[0])}
              {overlay}
            </div>
          </Col>
        </Row>
      </Grid>
    );
  }

  renderTwo() {
    const { images } = this.props;
    const { countFrom } = this.state;
    const overlay =
      images.length > countFrom && [2, 3].includes(+countFrom)
        ? this.renderCountOverlay(true)
        : this.renderOverlay();
    const conditionalRender =
      [3, 4].includes(images.length) ||
      (images.length > +countFrom && [3, 4].includes(+countFrom));

    return (
      <Grid>
        <Row>
          <Col xs={6} md={6} className="height-two">
            <div
              className="image background"
              onClick={this.openModal.bind(this, conditionalRender ? 1 : 0)}
              style={{
                background: `url(${
                  conditionalRender ? images[1].source : images[0].source
                })`
              }}
            >
              {this.renderVideoOverlay(
                conditionalRender ? images[1] : images[0]
              )}
              {this.renderOverlay()}
            </div>
          </Col>
          <Col xs={6} md={6} className="height-two">
            <div
              className="image background"
              onClick={this.openModal.bind(this, conditionalRender ? 2 : 1)}
              style={{
                background: `url(${
                  conditionalRender ? images[2].source : images[1].source
                })`
              }}
            >
              {this.renderVideoOverlay(
                conditionalRender ? images[2] : images[1]
              )}
              {overlay}
            </div>
          </Col>
        </Row>
      </Grid>
    );
  }

  renderThree(more) {
    const { images } = this.props;
    const { countFrom } = this.state;
    const overlay =
      !countFrom ||
      countFrom > 5 ||
      (images.length > countFrom && [4, 5].includes(+countFrom))
        ? this.renderCountOverlay(true)
        : this.renderOverlay(conditionalRender ? 3 : 4);
    const conditionalRender =
      images.length == 4 || (images.length > +countFrom && +countFrom == 4);

    return (
      <Grid>
        <Row>
          <Col xs={6} md={4} className="height-three">
            <div
              className="image background"
              onClick={this.openModal.bind(this, conditionalRender ? 1 : 2)}
              style={{
                background: `url(${
                  conditionalRender ? images[1].source : images[2].source
                })`
              }}
            >
              {this.renderVideoOverlay(
                conditionalRender ? images[1] : images[2]
              )}
              {this.renderOverlay(conditionalRender ? 1 : 2)}
            </div>
          </Col>
          <Col xs={6} md={4} className="height-three">
            <div
              className="image background"
              onClick={this.openModal.bind(this, conditionalRender ? 2 : 3)}
              style={{
                background: `url(${
                  conditionalRender ? images[2].source : images[3].source
                })`
              }}
            >
              {this.renderVideoOverlay(
                conditionalRender ? images[2] : images[3]
              )}
              {this.renderOverlay(conditionalRender ? 2 : 3)}
            </div>
          </Col>
          <Col xs={6} md={4} className="height-three">
            <div
              className="image background"
              onClick={this.openModal.bind(this, conditionalRender ? 3 : 4)}
              style={{
                background: `url(${
                  conditionalRender ? images[3].source : images[4].source
                })`
              }}
            >
              {this.renderVideoOverlay(
                conditionalRender ? images[3] : images[4]
              )}
              {overlay}
            </div>
          </Col>
        </Row>
      </Grid>
    );
  }

  renderVideoOverlay(image) {
    const { renderVideoOverlay } = this.props;
    return typeof renderVideoOverlay !== "undefined" &&
      typeof image.IsVideo !== "undefined" &&
      parseInt(image.IsVideo) === 1
      ? renderVideoOverlay()
      : "";
  }

  renderOverlay(id) {
    const { hideOverlay, renderOverlay, overlayBackgroundColor } = this.props;

    if (hideOverlay) {
      return false;
    }

    return [
      <div
        key={`cover-${id}`}
        className="cover slide"
        style={{ backgroundColor: overlayBackgroundColor }}
      ></div>,
      <div
        key={`cover-text-${id}`}
        className="cover-text slide animate-text"
        style={{ fontSize: "100%" }}
      >
        {renderOverlay()}
      </div>
    ];
  }

  renderCountOverlay(more) {
    const { images } = this.props;
    const { countFrom } = this.state;
    const extra = images.length - (countFrom && countFrom > 5 ? 5 : countFrom);

    return [
      more && <div key="count" className="cover"></div>,
      more && (
        <div
          key="count-sub"
          className="cover-text"
          style={{ fontSize: "2.5rem", fontWeight: 500 }}
        >
          <p>+{extra}</p>
        </div>
      )
    ];
  }

  render() {
    const { modal, index, countFrom } = this.state;
    const { images } = this.props;
    const imagesToShow = [...images];

    if (countFrom && images.length > countFrom) {
      imagesToShow.length = countFrom;
    }

    return (
      <div className="pety-images-grid">
        <div className="grid-container">
          {[1, 3, 4].includes(imagesToShow.length) && this.renderOne()}
          {imagesToShow.length >= 2 &&
            imagesToShow.length != 4 &&
            this.renderTwo()}
          {imagesToShow.length >= 4 && this.renderThree()}

          {modal && (
            <Modal onClose={this.onClose} index={index} images={images} />
          )}
        </div>
      </div>
    );
  }
}

Images.propTypes = {
  images: PropTypes.array.isRequired,
  hideOverlay: PropTypes.bool,
  renderOverlay: PropTypes.func,
  renderVideoOverlay: PropTypes.func,
  overlayBackgroundColor: PropTypes.string,
  onClickEach: PropTypes.func,
  countFrom: PropTypes.number
};

export default Images;
